/* Reset and base styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
}

/* App container */
.App {
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px;
  min-height: 100vh;
}

/* Header styles */
.App-header {
  text-align: center;
  margin-bottom: 40px;
  background: rgba(255, 255, 255, 0.95);
  padding: 30px;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
}

.App-header h1 {
  color: #2c3e50;
  font-size: 2.5rem;
  margin-bottom: 15px;
  font-weight: 700;
}

.App-header p {
  color: #666;
  font-size: 1.1rem;
  margin-bottom: 0;
}

/* Loading styles */
.loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 80vh;
  color: white;
}

.spinner {
  width: 50px;
  height: 50px;
  border: 5px solid rgba(255, 255, 255, 0.3);
  border-top: 5px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 20px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* No pictures message */
.no-pictures {
  grid-column: 1 / -1;
  text-align: center;
  padding: 60px 20px;
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
}

.no-pictures h3 {
  color: #2c3e50;
  font-size: 1.5rem;
  margin-bottom: 15px;
}

.no-pictures p {
  color: #666;
  font-size: 1.1rem;
}

/* Stats section */
.stats-section {
  background: rgba(255, 255, 255, 0.95);
  padding: 30px;
  border-radius: 20px;
  margin-bottom: 40px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
}

.stats-section h2 {
  color: #2c3e50;
  margin-bottom: 20px;
  text-align: center;
}

.stats-summary {
  text-align: center;
  margin-bottom: 30px;
  font-size: 1.1rem;
}

.stats-summary p {
  margin: 10px 0;
  color: #555;
}

/* Top 3 results display */
.top-three-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 25px;
  margin-top: 20px;
}

.top-three-item {
  text-align: center;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 15px;
  padding: 20px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  position: relative;
}

.top-three-item:hover {
  transform: translateY(-5px);
}

.top-three-item:first-child {
  background: linear-gradient(135deg, #ffd700, #ffed4e);
  border: 3px solid #ffd700;
}

.top-three-item:nth-child(2) {
  background: linear-gradient(135deg, #c0c0c0, #e5e5e5);
  border: 3px solid #c0c0c0;
}

.top-three-item:nth-child(3) {
  background: linear-gradient(135deg, #cd7f32, #daa520);
  border: 3px solid #cd7f32;
}

.medal {
  font-size: 3rem;
  margin-bottom: 10px;
  display: block;
}

.top-three-image {
  width: 150px;
  height: 150px;
  margin: 0 auto 15px;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
  border: 4px solid rgba(255, 255, 255, 0.9);
}

.top-three-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.top-three-info h3 {
  color: #2c3e50;
  margin-bottom: 8px;
  font-size: 1.2rem;
  font-weight: 600;
}

.top-three-info p {
  color: #555;
  font-weight: 500;
  font-size: 1rem;
}

/* Pictures grid */
.pictures-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 30px;
  margin-bottom: 40px;
}

/* Picture card */
.picture-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.picture-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
}

.picture-container {
  position: relative;
  height: 300px;
  overflow: hidden;
}

.picture-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.picture-card:hover .picture-container img {
  transform: scale(1.05);
}

.picture-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(to bottom, transparent 0%, rgba(0, 0, 0, 0.7) 100%);
  display: flex;
  align-items: flex-end;
  padding: 20px;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.picture-card:hover .picture-overlay {
  opacity: 1;
}

.vote-info {
  color: white;
  font-size: 1.2rem;
  font-weight: bold;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
}

.vote-count {
  display: flex;
  align-items: center;
  gap: 5px;
}

/* Picture actions */
.picture-actions {
  padding: 25px;
}

.picture-title {
  color: #2c3e50;
  margin-bottom: 15px;
  font-size: 1.1rem;
  font-weight: 600;
  text-align: center;
  line-height: 1.4;
}

.vote-button {
  width: 100%;
  background: linear-gradient(45deg, #e74c3c, #c0392b);
  color: white;
  border: none;
  padding: 15px 20px;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(231, 76, 60, 0.3);
}

.vote-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(231, 76, 60, 0.4);
  background: linear-gradient(45deg, #c0392b, #a93226);
}

.vote-button:disabled,
.vote-button.voting {
  background: linear-gradient(45deg, #95a5a6, #7f8c8d);
  cursor: not-allowed;
  transform: none;
  box-shadow: 0 2px 8px rgba(149, 165, 166, 0.3);
}

/* Footer */
.App-footer {
  text-align: center;
  padding: 30px;
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  backdrop-filter: blur(10px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.App-footer p {
  color: #666;
  font-size: 1rem;
  margin: 0;
}

/* Responsive design */
@media (max-width: 768px) {
  .App {
    padding: 15px;
  }
  
  .App-header {
    padding: 20px;
  }
  
  .App-header h1 {
    font-size: 2rem;
  }
  
  .pictures-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .picture-container {
    height: 250px;
  }
  
  .top-three-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .top-three-image {
    width: 120px;
    height: 120px;
  }
  
  .medal {
    font-size: 2.5rem;
  }
  
  .stats-section {
    padding: 20px;
  }
}

@media (max-width: 480px) {
  .App-header h1 {
    font-size: 1.5rem;
  }
  
  .picture-container {
    height: 200px;
  }
  
  .top-three-image {
    width: 100px;
    height: 100px;
  }
  
  .medal {
    font-size: 2rem;
  }
}

/* Popup Overlay */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease-in;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

/* Thank You Popup */
.thank-you-popup {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 20px;
  padding: 2rem;
  text-align: center;
  color: white;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  animation: bounceIn 0.6s ease-out;
  max-width: 400px;
  margin: 1rem;
}

@keyframes bounceIn {
  0% {
    transform: scale(0.3);
    opacity: 0;
  }
  50% {
    transform: scale(1.05);
  }
  70% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.thank-you-content h2 {
  margin: 1rem 0;
  font-size: 2rem;
  color: #fff;
}

.celebration {
  font-size: 4rem;
  animation: rotate 1s ease-in-out infinite alternate;
}

@keyframes rotate {
  from { transform: rotate(-10deg); }
  to { transform: rotate(10deg); }
}

.voted-image {
  margin: 1.5rem 0;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
}

.voted-image img {
  width: 150px;
  height: 150px;
  object-fit: cover;
}

.hearts {
  font-size: 2rem;
  animation: pulse 1s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

/* Leaderboard Popup */
.leaderboard-popup {
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 50%, #4facfe 100%);
  border-radius: 20px;
  padding: 2rem;
  text-align: center;
  color: white;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  animation: slideUp 0.6s ease-out;
  max-width: 500px;
  margin: 1rem;
  max-height: 80vh;
  overflow-y: auto;
}

@keyframes slideUp {
  from {
    transform: translateY(100px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.leaderboard-content h2 {
  margin: 0 0 2rem 0;
  font-size: 2.2rem;
  color: #fff;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.leaderboard-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.leaderboard-item {
  display: flex;
  align-items: center;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 15px;
  padding: 1rem;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  animation: slideInLeft 0.5s ease-out;
}

.leaderboard-item.rank-1 {
  animation-delay: 0.1s;
  transform: scale(1.05);
  box-shadow: 0 0 20px rgba(255, 215, 0, 0.5);
}

.leaderboard-item.rank-2 {
  animation-delay: 0.2s;
  box-shadow: 0 0 15px rgba(192, 192, 192, 0.5);
}

.leaderboard-item.rank-3 {
  animation-delay: 0.3s;
  box-shadow: 0 0 10px rgba(205, 127, 50, 0.5);
}

@keyframes slideInLeft {
  from {
    transform: translateX(-50px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.rank-medal {
  font-size: 2.5rem;
  margin-right: 1rem;
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

.rank-image {
  margin-right: 1rem;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.rank-image img {
  width: 60px;
  height: 60px;
  object-fit: cover;
}

.rank-info {
  flex: 1;
  text-align: left;
}

.rank-info h3 {
  margin: 0 0 0.5rem 0;
  font-size: 1.1rem;
  color: #fff;
}

.rank-info p {
  margin: 0;
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.9);
}

.leaderboard-footer {
  margin: 1.5rem 0 0 0;
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.9);
  font-style: italic;
}

/* Mobile Responsiveness for Popups */
@media (max-width: 768px) {
  .thank-you-popup,
  .leaderboard-popup {
    margin: 1rem;
    padding: 1.5rem;
    max-width: 90vw;
  }
  
  .thank-you-content h2,
  .leaderboard-content h2 {
    font-size: 1.5rem;
  }
  
  .celebration {
    font-size: 3rem;
  }
  
  .voted-image img {
    width: 120px;
    height: 120px;
  }
  
  .rank-medal {
    font-size: 2rem;
  }
  
  .rank-image img {
    width: 50px;
    height: 50px;
  }
  
  .leaderboard-item {
    padding: 0.8rem;
  }
} 